    color: #fff;
    bottom: 50px;
    right: 2%;
    width: 60px;
    height: 60px; }
    #Advert #bt_addProduct:hover {
      -webkit-box-shadow: -3px 7px 6px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
